<template>
	<div class="tableBody">
		<table class="tableBody" border="1" cellspacing="0" cellpadding="0">
			<thead>
				<tr class="tableBody-tr">
					<th colspan="2" >项目及考核内容</th>
					<th colspan="2"> 分值	</th>
					<th>得分合计</th>
					<th>操作</th>
				</tr>
			</thead>
			<tbody  >
				<tr v-for="item in tableData" :key="item.id" class="tableBody-tr tableBody-tr-two"  >
					<td v-show="item.isHidden" :rowspan="item.rowspan"  class="vertical" >
						{{item.type}}
					</td>
					<td>{{item.content}} </td>
					<td>{{item.cate}}</td>
					<td>{{item.score}}</td>
					<td>{{item.score}}</td>
					<td class='td-text-center'>详情	</td>
				</tr>

			</tbody>
		</table>
		<div>
			<img src="../../assets/img/logo.png" alt="">

		</div>
	</div>
</template>

<style>
.tableBody{
	width: 100%;
	padding:6px 12px;
	font-size: 16px;
}

.tableBody-tr{
	width: 100%;
	border:1px solid #000;
}

.vertical{
	vertical:center;
}
tr > th{
	padding:4px 0;
}
tr > td{
	padding:4px 0;
	max-width:60px;
	word-wrap: break-word;
	font-size: 14px;
}
.td-text-center{
	text-align: center;
}

</style>

<script>
	export default {
		name: 'tableBody',
		data(){
			return{
				msg:'this is tableBody.vue test',
				tableData:[
				{
					"type":"出勤考核5%","content":"全勤","cate":"优秀","score":"5","zsld":"张三","qtld":"李四","bmzj":"杨广",
					"rowspan":"","isHidden":false
				},
				{
					"type":"出勤考核5%","content":"全勤","cate":"优秀","score":"5","zsld":"张三","qtld":"李四","bmzj":"杨广","rowspan":"","isHidden":false
				},
				{
					"type":"日常管理5%","content":"全勤","cate":"优秀","score":"5","zsld":"张三","qtld":"李四","bmzj":"杨广",
					"rowspan":"","isHidden":false
				},
				{
					"type":"出勤考核5%","content":"全勤","cate":"优秀","score":"5","zsld":"张三","qtld":"李四","bmzj":"杨广","rowspan":"","isHidden":false
				},
				{
					"type":"日常管理5%","content":"全勤","cate":"优秀","score":"5","zsld":"张三","qtld":"李四","bmzj":"杨广","rowspan":"","isHidden":false
				}
				],
				newTableData:[]

			}
		},
		methods:{
			////数组对象排序 原理利用sort()
			listSort(prop){
				return function (obj1, obj2) {
					var val1 = obj1[prop];
					var val2 = obj2[prop];if (val1 < val2) {
						return -1;
					} else if (val1 > val2) {
						return 1;
					} else {
						return 0;
					}
				}
			},
			//// getRepeatNum 获取数组中重复值的个数
			getRepeatNum(titleList){
				let obj = {};
				let arr = [];
				let pushArr= []
				for(let i= 0, l = titleList.length; i< l; i++){
					let item = titleList[i].type;
					obj[item] = (obj[item] +1 ) || 1;
				}

				for(let j in obj){
					var indexOf4 = (titleList || []).findIndex((item) => item.type == j);
					arr.push(indexOf4);
				}

				console.log(obj,arr);
				return [obj,arr];
			},
			//// 更改数组对象中的元素
			getResetList(list){
				let objList = this.$options.methods.getRepeatNum(this.tableData.sort(this.$options.methods.listSort("type")));
				for (var i = 0;i < list.length;i++) {
					for(let j in objList[0]){
						if(list[i].type == j){
							list[i].rowspan = objList[0][j];
						}
					};
					for (let j in objList[1]) {
						if(i == objList[1][j]){
							list[i].isHidden = true;
						}
					};
				}
				return list;
			}
		},

		created:function(){

			this.newTableData = this.getResetList(this.tableData);


		},
		beforeMount:function(){
			console.log(this.newTableData);


		},
		mounted:function(){


		}


	}
</script>



